import { Ref, ref, watch } from "vue";

type modelType = string | boolean

export function useVModel(): Ref<modelType> {
  const props = defineProps({
    modelValue: [String, Boolean]
  });
  const innerValue = ref<modelType>(props.modelValue as modelType);
  const $emit = defineEmits(['update:modelValue']);
  watch(() => props.modelValue as modelType, (value: modelType) => innerValue.value = value)
  watch(() => innerValue.value, value => $emit('update:modelValue', value))
  return innerValue
}