/**
 * 数据类型
 */
export enum EnumType {
  Number = '[object Number]',
  Boolean = '[object Boolean]',
  Object = '[object Object]',
  Array = '[object Array]',
  Function = '[object Function]',
  String = '[object String]',
  RegExp = '[object RegExp]',
  Blob = '[object Blob]',
  File = '[object File]',
  Date = '[object Date]',
  Error = '[object Error]',
  Promise = '[object Promise]',
  Undefined = '[object Undefined]',
  Null = '[object Null]',
  AsyncFunction = '[object AsyncFunction]'
}

/**
 * Console type
 */
export enum EnumConsole {
  debug = 'debug',
  error = 'error',
  info = 'info',
  warn = 'warn'
}

/**
 * Time type
 */
export enum EnumTime {
  ms, hour, minute, second
}

/**
 * Console Color
 */
export enum EnumColor {
  white = '37m',
  red = '31m',
  green = '32m',
  yellow = '33m',
  blue = '34m'
}

/**
 * Layout Direction
 */
export enum EnumDirection {
  vertical, horizontal
}

/**
 * Data 排序
 */
export enum EnumOrder { ASC, DESC }

/**
 * 格式化
 */
export enum EnumFormat {
  FlatMap,
  URlParams
}

export enum FileSuffix {
  xml, ts, java, json, md, vue, html, css, scss, properties, sql, sh, yaml, conf, txt, js
}