import { clientRect } from '../script/Unclassified';
import { Ref, ref, unref, reactive, provide } from 'vue';

export interface IStatus {
  statusAdd: Ref<boolean>;
  statusUpdate: Ref<boolean>;
  statusDetail: Ref<boolean>;
}

export interface IPrimaryKey {
  primaryKeyAdd: Ref<string | number>;
  primaryKeyRemove: Ref<string | number>;
  primaryKeyUpdate: Ref<string | number>;
  primaryKeyDetail: Ref<string | number>;
}

export interface IForm {
  refForm: Ref<any>;
  refFormList: Ref<any>;
  refFormAdd: Ref<any>;
  refFormUpdate: Ref<any>;
  refFormDetail: Ref<any>;
}

export interface IContainer {
  containerHeight: Ref<number>;
  containerTarget: Ref<string>;
  containerInitialize: () => void;
}

export interface ITable {
  selectionMultiple: Ref<any[]>;
  selectionChange: (selection: any) => void;
}

export interface IModel {
  modelResult: any;
  modelPagination: IPagination;
}

export interface IPagination {
  pageSize: number;
  pageNum: number;
  [key: string]: any
}

export function useStatus(): IStatus {
  const statusAdd = ref<boolean>(false);
  const statusUpdate = ref<boolean>(false);
  const statusDetail = ref<boolean>(false);
  return { statusAdd, statusUpdate, statusDetail };
}

export function usePrimaryKey(): IPrimaryKey {
  const primaryKeyAdd = ref<string | number>();
  const primaryKeyRemove = ref<string | number>();
  const primaryKeyUpdate = ref<string | number>();
  const primaryKeyDetail = ref<string | number>();
  return { primaryKeyAdd, primaryKeyRemove, primaryKeyUpdate, primaryKeyDetail };
}

export function useForm(): IForm {
  const refForm = ref();
  const refFormList = ref();
  const refFormAdd = ref();
  const refFormUpdate = ref();
  const refFormDetail = ref();
  return { refForm, refFormList, refFormAdd, refFormUpdate, refFormDetail };
}

export function useContainer(): IContainer {
  const heightMin = 300;
  const containerHeight = ref();
  const containerTarget = ref();
  function containerInitialize(): void {
    const { height } = clientRect(unref(containerTarget));
    containerHeight.value = heightMin < height ? Math.floor(height) : heightMin;
  }
  return { containerHeight, containerTarget, containerInitialize };
}

/**
 * use table
 * @returns 
 */
export function useTable(): ITable {
  const selectionMultiple = ref([]);
  function selectionChange(selection: any): void {
    selectionMultiple.value = selection;
  }
  return { selectionMultiple, selectionChange };
}

export function useModel(): IModel {
  const modelResult = reactive({ list: [], total: 0 });
  provide('modelResult', modelResult);
  const modelPagination: IPagination = { pageSize: 20, pageNum: 1 };
  provide('modelPagination', modelPagination);
  return { modelResult, modelPagination };
}