import { isString, isObject, getType } from './Type';
import { ErrorConsole } from './CustomError';
import * as Assert from './Assert';
import { isBlank, isEmpty } from './Common';
import { EnumDirection, EnumFormat, EnumOrder, EnumType } from './DefineEnum';
import { IOptionAssign, IOptionClone, IOptionEqual } from './DefineInterface';
import { toURL } from './Convert';
import { ProxyConsole } from './DefineProxy';

/**
 * 数组排序
 * @param target 要排序的数组
 * @param callback 排序的转换回调
 * @param order 排序方式, 默认：EnumOrder.ASC
 * @returns 
 */
export function sorting<T>(target: T[], callback: (a: any) => void , order?: EnumOrder): T[] {
  if (isBlank(target)) { return target; }
  target.sort((a, b) => {
    let valueA: any = callback(a);
    let valueB: any = callback(b);
    if (isString(valueA) === false) { valueA = JSON.stringify(valueA); }
    if (isString(valueB) === false) { valueB = JSON.stringify(valueB); }
    if (valueA === valueB) { return 0; }
    if (order === EnumOrder.DESC) {
      return valueB > valueA ? 1 : -1;
    }
    // 默认升序
    return valueA > valueB ? 1 : -1;
  });
  return target;
}

/**
 * 字符串加密
 * @param target 目标字符串
 * @returns 
 */
export function encryption(target: string): string {
  return window.btoa(encodeURIComponent(target));
}

/**
 * 字符串加密
 * @param target 目标字符串
 * @returns 
 */
export function decryption(target: string): string {
  return encodeURIComponent(window.atob(target));
}

/**
 * 在对象内找元素
 * @param target 目标对象
 * @param path 路径
 * @returns 
 */
export function findPath(target: any, path: string): string {
  Assert.isObject(target, 'target is not Object');
  if (path.includes('.') === false) {
    return Reflect.get(target, path);
  }
  const list = path.split('.');
  let tempObject = { ...target };
  for (const item of list) {
    if (isEmpty(item) || isObject(tempObject) === false || Reflect.has(tempObject, item) === false) {
      return '';
    }
    tempObject = Reflect.get(tempObject, item);
  }
  return tempObject;
}

/**
 * 清空数组 or 对象
 * @param target 
 */
export function clearEmpty<T>(target: T): T {
  if (target instanceof Array) {
    target.splice(0, target.length);
  }

  if (isObject(target)) {
    for (const key of Object.keys(target)) {
      Reflect.deleteProperty(target as object, key);
    }
  }
  return target;
}

/**
  * 生成 UUID
  */
export function UUID(): string {
  const s: any[] = [];
  const hexDigits = '0123456789abcdef';
  for (let i = 0; i < 36; i++) {
    s.push(hexDigits.substring(Math.floor(Math.random() * 0x10), 1));
  }
  s[14] = '4'; // bits 12-15 of the time_hi_and_version field to 0010
  s[19] = hexDigits.substring((s[19] & 0x3) | 0x8, 1); // bits 6-7 of the clock_seq_hi_and_reserved to 01
  s[8] = s[13] = s[18] = s[23] = '-';
  return s.join('');
}


/**
 * 数组是否包含某元素
 * @param target 
 * @param fn 
 * @returns 
 */
export function includes(target: any[], fn: number | string | ((item: any) => boolean)): boolean {
  if (fn instanceof Function) {
    return isEmpty(target.find(item => fn(item))) === false;
  }
  return target.includes(fn);
}

/**
 * 获取元素某个方向上是否有滚动条
 * @param el html 元素
 * @param direction 方向，默认：EnumDirection.vertical
 * @returns 
 */
export function hasScrollBar(el: HTMLElement, direction: EnumDirection): boolean {
  if (isEmpty(direction)) { direction = EnumDirection.vertical; }
  return direction === EnumDirection.vertical ? el.scrollHeight > el.clientHeight : el.scrollWidth > el.clientWidth;
}

/**
 * 获取滚动条的宽度
 * @returns 
 */
export function scrollBarWidth(): number {
  const el: HTMLElement = document.createElement('div');
  Object.assign(el.style, { width: '100px', height: '100px', overflowY: 'scroll' });
  document.body.appendChild(el);
  const difference: number = el.offsetWidth - el.clientWidth;
  document.body.removeChild(el);
  return difference;
}

/**
 * 字符串化
 * @param target 目标对象
 * @returns 
 */
export function stringify(target: any, format: EnumFormat): string | object | void {
  if (format === EnumFormat.FlatMap) {
    if (isObject(target)) {
      return stringifyObject(target);
    }

    if (target instanceof Array) {
      return stringifyArray(target);
    }
  }

  if (format === EnumFormat.URlParams) {
    return new URLSearchParams(target).toString();
  }
}

export function stringifyObject(target: object, parentKey?: string): object {
  Assert.isObject(target, '参数：target 必须是对象');

  for (const key in target) {
    const value = Reflect.get(target, key);
    if (isObject(value)) {
      const newKey = parentKey ? `${parentKey}.${key}` : key;
      Object.assign(target, stringifyObject(value, newKey));
      Reflect.deleteProperty(target, key);
      continue;
    }
    if (parentKey) {
      Reflect.set(target, `${parentKey}.${key}`, value);
      Reflect.deleteProperty(target, key);
    }
    if (value instanceof Array) {
      const newKey = parentKey ? `${parentKey}.${key}` : key;
      Object.assign(target, stringifyArray(value, newKey));
      Reflect.deleteProperty(target, key);
      continue;
    }
  }
  return target;
}

export function stringifyArray(target: any[], parentKey?: string): object {
  const map: object = {};
  target.forEach((value, index) => {
    ProxyConsole.debug(index);
    const key: string = parentKey ? `${parentKey}[${index}]` : `[${index}]`;
    if (isObject(value)) {
      Object.assign(map, stringifyObject(value, key));
      return;
    }
    if (value instanceof Array) {
      Object.assign(map, stringifyArray(value, key));
      return;
    }
    Reflect.set(map, key, value);
  });

  return map;
}
/**
 * 对象化
 * @param target 目标字符串
 * @returns 
 */
export function parse(target: string): object {
  const searchParams: URLSearchParams = new URLSearchParams(target);
  const result = {};
  searchParams.forEach((value: string, key: string) => {
    Reflect.set(result, key, value);
  });
  return result;
}

/**
* 获取 Url 参数
* @param {String} key 参数名
* @param {String} search 参数集
*/
export function urlParam(key: string, search: string = location.search): string {
  return new URLSearchParams(search).get(key);
}
/**
 * 获取 body 的高度
 * @returns 
 */
export function clientHeight(): number {
  return document.body.clientHeight;
}

/**
 * 比较两个对象是否同一个
 * @param target 目标对象
 * @param source 目标对象
 * @param uniqueKey 唯一属性
 * @param useContent 使用值比较
 * @returns 
 */
export function equal(target: any, source: any, option?: IOptionEqual): boolean {
  if (isObject(target) === false || isObject(source) === false) {
    throw new ErrorConsole('target and source 必须都为 Object');
  }
  if (isString(option?.uniqueKey)) {
    const valueTarget = Reflect.get(target, option?.uniqueKey as string);
    const valueSource = Reflect.get(source, option?.uniqueKey as string);
    if (option?.useContent === true) {
      return JSON.stringify(valueTarget) === JSON.stringify(valueSource);
    }
    return valueTarget === valueSource;
  }
  throw new ErrorConsole('参数：option.uniqueKey can\'t be blank String。');
}

/**
 * 合并对象
 * <p>option.deep -> 深度合并。</p>
 * <p>option.strict = true 严格模式 -> 相同 Key，类型不同 忽略</p>
 * <p>option.level -> 根据层级进行合并。</p>
 * <p>option.uniqueKey -> object.uniqueKey</p>
 * <p>option.ignoreEmpty -> 忽略空值</p>
 * <p>option.ignoreKey -> 忽略属性</p>
 * @param target 目标对象
 * @param source 源对象
 * @param option 参数
 * @returns 
 */
export function assignObject(target: object, source: object, option?: IOptionAssign): object {
  const typeEmpty: EnumType[] = [EnumType.Undefined, EnumType.Null];
  if (isObject(source) === false) {
    throw new ErrorConsole('source 必须都为 Object');
  }

  if (isObject(target) === false) {
    if (option.strict === true) {
      throw new ErrorConsole('严格模式：target 必须都为 Object');
    }
    ProxyConsole.warn('target is not object；赋值：target = {}');
    target = {};
  }

  function handlerExclude(key: string, target: string[]): string[] {
    const searchValue = `${key}.`;
    return target.filter((item: string) => item.startsWith(searchValue)).flatMap((item: string) => item.replace(searchValue, ''));
  }

  for (const key in source) {
    if (option?.ignoreKey && option.ignoreKey.includes(key)) { continue; }
    const valueTarget = Reflect.get(target, key);
    const valueSource = Reflect.get(source, key);
    const typeTarget: EnumType = getType(valueTarget);
    const typeSource: EnumType = getType(valueSource);
    if (option?.ignoreEmpty === true && typeEmpty.includes(typeSource)) {
      ProxyConsole.warn(`忽略空模式：source.${key} type is ${typeSource}；合并忽略`);
      continue;
    }
    if (option?.strict === true && Reflect.has(target, key) === false) {
      ProxyConsole.warn(`严格模式：target not have 属性：${key}；合并忽略`);
      continue;
    }
    if (typeSource === EnumType.Object) {
      const ignoreKey: string[] = handlerExclude(key, option?.ignoreKey || []);
      if (typeTarget !== EnumType.Object) {
        Reflect.set(target, key, {});
      }
      // 剔除父级属性
      if (option?.deep) {
        const value: object = assignObject(Reflect.get(target, key), valueSource, { ...option, ignoreKey });
        Reflect.set(target, key, value);
        continue;
      }
      if (option?.level && option?.level > 1) {
        const value: object = assignObject(Reflect.get(target, key), valueSource, { ...option, ignoreKey, level: option?.level - 1 });
        Reflect.set(target, key, value);
        continue;
      }
    }
    if (typeSource === EnumType.Array) {
      const uniqueKey = isObject(option?.uniqueKey) ? Reflect.get(option?.uniqueKey as object, key) : undefined;
      if (isBlank(uniqueKey)) { continue; }

      if (typeTarget !== EnumType.Array) {
        Reflect.set(target, key, []);
      }
      if (option?.deep) {
        const value: any[] = assignArray(Reflect.get(target, key), valueSource, { ...option, uniqueKey });
        Reflect.set(target, key, value);
        continue;
      }
      if (option?.level && option?.level > 1) {
        const value: any[] = assignArray(Reflect.get(target, key), valueSource, { ...option, uniqueKey, level: option?.level - 1 });
        Reflect.set(target, key, value);
        continue;
      }
    }
    Reflect.set(target, key, valueSource);
  }
  return target;
}

/**
 * 合并对象
 * <p>option.deep -> 深度合并。</p>
 * <p>option.strict = true 严格模式 -> 相同 Key，类型不同 忽略</p>
 * <p>option.level -> 根据层级进行合并。</p>
 * <p>option.uniqueKey -> object.uniqueKey</p>
 * <p>option.ignoreEmpty -> 忽略空值</p>
 * <p>option.ignoreKey -> 忽略属性</p>
 * @param target 目标对象
 * @param source 源对象
 * @param option 参数
 * @returns 
 */
export function assignArray(target: any[], source: any[], option?: IOptionAssign): any[] {
  if (isBlank(option?.uniqueKey)) {
    throw new ErrorConsole('参数：option.uniqueKey can\'t be blank。');
  }
  for (const valueSource of source) {
    if (isObject(valueSource)) {
      const valueTarget: any = target.find((valueTarget: any) => equal(valueTarget, valueSource, option));
      if (option?.strict === true && isBlank(valueTarget)) {
        continue;
      }
      if (isObject(valueTarget)) {
        /** target and source type is object */
        assignObject(valueTarget, valueSource, option);
        continue;
      }
    }
    target.push(valueSource);
  }
  return target;
}

/**
 * 合并对象
 * <p>option.deep -> 深度合并。</p>
 * <p>option.strict = true 严格模式 -> 相同 Key，类型不同 忽略</p>
 * <p>option.level -> 根据层级进行合并。</p>
 * <p>option.uniqueKey -> object.uniqueKey</p>
 * <p>option.ignoreEmpty -> 忽略空值</p>
 * <p>option.ignoreKey -> 忽略属性</p>
 * @param target 目标对象
 * @param source 源对象
 * @param option 参数
 * @returns 
 */
export function assign(target: any | any[], source: any | any[], option?: IOptionAssign): any {
  if (target instanceof Array && source instanceof Array) {
    return assignArray(target, source, option);
  }
  if (isObject(target) && isObject(source)) {
    return assignObject(target, source, option);
  }
  throw new ErrorConsole(`target type is ${getType(target)}, source type is ${getType(source)}；type 必须为 array or object.`);
}

/**
 * 
 * @param target 目标克隆对象
 * @param option 克隆参数 
 * @returns 
 */
export function clone<T>(target: T, option?: IOptionClone): T {
  Assert.isObject(target, '参数：target 必须为对象');
  if (option === undefined) { option = { deep: false }; }
  if (option.deep === true) {
    try {
      return JSON.parse(JSON.stringify(target));
    } catch (error) {
      ProxyConsole.error('Tools.clone --> deep = true，target = ', target);
    }
  }
  const newTarget = { ...target };
  if (option.exclude !== undefined) {
    // 排除掉不用的属性
    for (const key of option.exclude) { Reflect.deleteProperty(newTarget as object, key); }
    // 排除掉不用的属性
  }
  return newTarget;
}


/**
 * 元素的大小及其相对于视口的位置
 * @param target 目标元素
 * @returns 
 */
export function clientRect(target: HTMLElement | any): DOMRect {
  if (isEmpty(target) === false) {
    const element = target.$el || target;
    if (element instanceof HTMLElement) {
      return element.getBoundingClientRect();
    }
  }
  return new DOMRect();
}

/**
 * 获取 cookie key 对应的 value
 * @param key 键名称
 * @returns 
 */
export function cookie(key: string): string {
  const regExp = new RegExp('(^| )' + key + '=([^;]*)(;|$)');
  const match = document.cookie.match(regExp);
  return match === null || match.length < 3 ? '' : unescape(match[2]);
}

/**
 * 异常信息
 * @param target 目标
 * @since v0.0.7 以后废弃
 * @deprecated Use `parseMessage('base64')` instead.
 * @returns 
 */
export function message(target: any): string {
  if (isEmpty(target)) { return ''; }
  if (target instanceof Error) {
    return target.message;
  }
  if (isObject(target)) {
    if (Reflect.has(target, 'body')) {
      return target.body?.message;
    }
    if (Reflect.has(target, 'message')) {
      return target.message;
    }
  }
  return target;
}


/**
 * 解析：异常信息
 * @param target 目标
 * @returns 
 */
export function parseMessage(target: any): string {
  if (isEmpty(target)) { return ''; }
  if (target instanceof Error) {
    return target.message;
  }
  if (isObject(target)) {
    if (Reflect.has(target, 'body')) {
      return target.body?.message;
    }
    if (Reflect.has(target, 'message')) {
      return target.message;
    }
  }
  return target;
}

/**
 * 重定向
 * @param target 目标地址
 * @param location 
 */
export function sendRedirect (target: string, lct?: Location): void {
  const url: URL = toURL(target);
  (lct || location).href = url.href;
}
