import { IPoint } from './DefineInterface';

export default class Layout {
  private __data: IPoint[] = []
  private __max: number
  private __dataSource: IPoint[] = []
  private __pointList: IPoint[] = []
  private __denominator: number
  private __height: number
  constructor(data: IPoint[], denominator: number = 12, screenHeight: number) {
    this.__dataSource = data
    this.__max = denominator + 1
    this.__denominator = denominator
    this.__pointList = [{ row: 1, column: 1 }]
    this.__height = screenHeight
  }

  public get pointList(): IPoint[] {
    return this.__pointList
  }

  public set pointList(value: IPoint[]) {
    this.__pointList = value
  }

  public get data(): IPoint[] {
    return this.__data
  }

  public get areaHeight(): number {
    return Math.round(this.__height / this.__denominator)
  }

  public get containerStyle(): any {
    return {
      display: 'grid',
      'grid-template-columns': `repeat(${this.__denominator}, 1fr)`,
      'grid-auto-rows': `${this.areaHeight}px`
    }
  }

  public itemStyle({ row, column }: IPoint) {
    if (row && column) {
      return {
        'grid-row': `${row.start} / ${row.end}`,
        'grid-column': `${column.start} / ${column.end}`
      }
    }
    return {}
  }

  public include({ start, end }: any, value: number): boolean {
    value += 0.1
    return value >= start && value <= end
  }

  public includePoint({ row, column }: IPoint): boolean {
    for (const item of this.data) {
      if (this.include(item.row, row) && this.include(item.column, column)) {
        return true
      }
    }
    return false
  }

  public mergeArea(): void {
    for (const point of this.pointList) {
      if (this.includePoint(point)) {
        Object.assign(point, { disabled: true })
      }
    }
  }

  orderArea(): void {
    this.pointList.sort((a, b) => {
      if (a.row === b.row) {
        return a.column - b.column
      }
      return a.row - b.row
    })
  }

  public parseAllArea() {
    for (let item of this.__dataSource) {
      const data = this.parseArea(item)
      if (data.row && data.column) {
        this.__data.push(data)
        this.parsePoint()
        item.row = data.row
        item.column = data.column
      }
    }
  }

  public removeMarkArea() {
    const length = this.pointList.length
    for (let index = length - 1; index >= 0; index--) {
      const item = this.pointList[index]
      if (item.disabled === true) {
        this.pointList.splice(index, 1)
      }
    }
  }

  public crossVerify(target: any, { start, end }: any) {
    if (start + 0.001 > target.end || end - 0.001 < target.start) {
      return false
    }
    return true
  }

  public crossArea({ row, column }: IPoint): boolean {
    for (const item of this.data) {
      if (this.crossVerify(item.row, row) && this.crossVerify(item.column, column)) {
        return true
      }
    }
    return false
  }

  /**
   * 点位向上移动， 每次减 1
   * @param {Object} param
   */
  public extendPointUpAndPush({ row, column }: IPoint): void {
    let temp = { row, column }
    let count = 0
    do {
      count++
      if (count > 100) { break }
      temp = { row: temp.row - 1, column }
      if (temp.row === 0 || this.includePoint(temp)) {
        this.pointList.push({ row: temp.row + 1, column })
        return
      }
    } while (true)
  }

  /**
   * 点位向左移动， 每次减 1
   * @param {Object} param
   */
  public extendPointLeftAndPush({ row, column }: IPoint): void {
    let temp = { row, column }
    let count = 0
    do {
      count++
      if (count > 100) { break }
      temp = { row, column: temp.column - 1 }
      if (temp.column === 0 || this.includePoint(temp)) {
        this.pointList.push({ row, column: temp.column + 1 })
        return
      }
    } while (true)
  }

  /**
   * 点位解析
   */
  public parsePoint(): void {
    this.pointList = []
    for (const item of this.data) {
      if (item.column.end < this.__max) {
        this.extendPointUpAndPush({
          column: item.column.end,
          row: item.row.start
        })
      }

      this.extendPointLeftAndPush({
        column: item.column.start,
        row: item.row.end
      })
    }
    this.orderArea(/** 区域排序 */)
    this.mergeArea(/** 区域合并 */)
    this.removeMarkArea(/** 区域移出 */)
  }

  public parseArea(item: any): any {
    const { rowspan, colspan, ...other } = item
    if (rowspan > this.__max - 1 || colspan > this.__max - 1) {
      throw new Error('数据异常，请检查。')
    }
    const result = { ...other }
    let storage = false

    for (const point of this.pointList) {
      const { row, column } = point
      if (point.disabled === true) { continue }
      if (storage === true) { break }
      if (column + colspan > this.__max) { continue }

      const temp = {
        row: { start: row, end: row + rowspan },
        column: { start: column, end: column + colspan }
      }
      if (this.crossArea(temp)) { continue }
      Object.assign(result, temp)
      point.disabled = storage = true
    }
    return result
  }
}
