import { createRouter, createWebHistory, NavigationFailure, RouteLocationNormalized, Router, RouteRecordRaw, RouterHistory } from 'vue-router';
import { ProxyConsole } from './DefineProxy';
import { toJoin, toURL } from './Convert';
import { session } from './CustomStorage';

export interface ISecurityRouter {
  enable: () => boolean;
  enableAuthority?: () => boolean;
  beforeEach: (to: RouteLocationNormalized, form: RouteLocationNormalized) => any;
  beforeResolve: (to: RouteLocationNormalized) => any;
  afterEach: (to: RouteLocationNormalized, from: RouteLocationNormalized, failure?: NavigationFailure) => any;
}

export interface IOptionRouter {
  history?: RouterHistory;
  modules?: object,
  routes: RouteRecordRaw[];
}

function inintRouteRecord(router: Router, modules: object) {
  ProxyConsole.debug(`gold-core：RouterVue —— inintRouteRecord`)
  if (modules) {
    const prefix = '/src/views';
    const suffix = 'Route.vue';
    const routes: RouteRecordRaw[] = []
    for (const key of Object.keys(modules)) {
      const path = toJoin(key.replace(prefix, '').replace(suffix, ''))
      router.addRoute({ path, component: Reflect.get(modules, key) })
      routes.push({ path, component: Reflect.get(modules, key) })
    }
    session.set('route-record-list', routes.map(({ path }) => path))
  }
}

export const SecurityRouter: ISecurityRouter = {
  enable() { return false; },
  beforeEach(to: RouteLocationNormalized, from: RouteLocationNormalized): any {
    ProxyConsole.warn(`请实现回调函数：SecurityRouter.${SecurityRouter.beforeEach.name}`, toURL(to.fullPath).searchParams.get('token'), from);
    return true;
  },
  beforeResolve(to: RouteLocationNormalized): any {
    ProxyConsole.warn(`请实现回调函数：SecurityRouter.${SecurityRouter.beforeEach.name}`, to);
    return true;
  },
  afterEach(to: RouteLocationNormalized, from: RouteLocationNormalized, failure?: NavigationFailure): any {
    ProxyConsole.warn(`请实现回调函数：SecurityRouter.${SecurityRouter.afterEach.name}`, to, from, failure);
  }
};

export function createRouterVue(option: IOptionRouter): Router {
  const router: Router = createRouter(Object.assign({ history: createWebHistory() }, option));
  router.beforeEach((to: RouteLocationNormalized, from: RouteLocationNormalized): any => {
    ProxyConsole.debug(`gold-core：RouterVue —— beforeEach`)
    if (SecurityRouter.enable()) {
      return SecurityRouter.beforeEach(to, from)
    }
  });
  router.beforeResolve((to: RouteLocationNormalized): any => {
    ProxyConsole.debug(`gold-core：RouterVue —— beforeResolve`)
    if (SecurityRouter.enable()) {
      return SecurityRouter.beforeResolve(to)
    }
  });
  router.afterEach((to: RouteLocationNormalized, from: RouteLocationNormalized, failure?: NavigationFailure): any => {
    ProxyConsole.debug(`gold-core：RouterVue —— afterEach`)
    if (SecurityRouter.enable()) {
      return SecurityRouter.afterEach(to, from, failure)
    }
  });
  inintRouteRecord(router, option.modules)
  return router;
}
