import { isObject, isString } from './Type';

/**
 * 判断：非空对象
 * @param target 目标
 * @returns 
 */
export function notBlankObject(target: any): boolean {
  if (isEmpty(target)) { return false }
  return isObject(target) && Object.keys(target).length > 0
}

/**
 * 判断：非空数组
 * @param target 目标
 * @returns 
 */
export function notBlankArray(target: any): boolean {
  if (isEmpty(target)) { return false }
  return target instanceof Array && target.length > 0
}

/**
 * 判断：是否为 empty
 * @param target 目标
 * @returns 
 */
export function isEmpty(target: any): boolean {
  return target === null || target === undefined
}

/**
 * 判断：是否为 空串
 * @param target 目标
 * @returns 
 */
export function trim(target: string): string {
  return target.replace(/^[\s\uFEFF\xA0]+|[\s\uFEFF\xA0]+$/g, '')
}

/**
 * 判断：是否为空串
 * @param target 目标
 * @returns
 */
export function isBlank(target: any): boolean {
  if (isEmpty(target)) { return true }
  if (isString(target)) {
    const value = trim(target)
    return value === '' || value === 'null' || value === 'undefined'
  }
  if (target instanceof Array) {
    return target.length === 0
  }
  if (isObject(target)) { return Object.keys(target).length === 0 }
  return false
}