import { isEmpty } from './Common';

/**
 * 元素是否包含 class name
 * @param element Html Element
 * @param value class name
 * @returns 
 */
export function hasClass(element: HTMLElement, value: string): boolean {
  const classList: DOMTokenList = element.classList
  return isEmpty(classList) ? false : classList.contains(value)
}

/**
 * 元素添加 class name
 * @param element Html Element
 * @param value class name
 */
export function addClass(element: HTMLElement, value: string) {
  const classList: DOMTokenList = element.classList
  if (classList.contains(value) === false) {
    classList.add(value)
  }
}

/**
 * 元素移除 class name
 * @param element Html Element
 * @param value class name
 */
export function removeClass(element: HTMLElement, value: string) {
  const classList: DOMTokenList = element.classList
  if (classList.contains(value)) {
    classList.remove(value)
  }
}