import { isEmpty } from "./Common";
import { EnumConsole } from "./DefineEnum";
export const ProxyConsole = new Proxy(console, {
  get(target, propKey) {
    const originalMethod = target[propKey];
    if (originalMethod instanceof Function && propKey === EnumConsole.debug) {
      const session = globalThis.window?.sessionStorage
      if (isEmpty(session.getItem('gold-debugger')) === false) {
        return originalMethod;
      }
      return (): void => {}
    }
    return originalMethod;
  }
});