import { isBlank, isEmpty } from './Common';
import { TypeString } from './DefineType';
import { ProxyConsole } from './DefineProxy';
import { isObject } from './Type';
import { encryption } from './Unclassified';

/**
 * Storage Option
 */
export interface IOptionStorage<T> {
  encrypt?: boolean;
  defaultValue?: T;
}

const prefix = 'encrypt__';
/**
 * storage 通过 Key 存储
 * @param key storage key
 * @param option { defaultValue：'默认值' }
 * @returns 返回目标
 */
function get<T>(storage: Storage, key: string, option?: IOptionStorage<T>): T {
  ProxyConsole.debug(`storage.get：key = ${key}`);
  const value: TypeString = storage.getItem(key);
  if (isEmpty(value)) {
    return option?.defaultValue;
  }
  try {
    if (value?.startsWith(prefix)) {
      return JSON.parse(encryption(value.replace(prefix, '')));
    }
    return JSON.parse(value as string);
  } catch (error) {
    ProxyConsole.warn(`JSON.parse ${value} 异常：${error}.`, error);
    return undefined;
  }
}

/**
  * storage set value
  * @param key storage key
  * @param value storage value
  * @param option IOptionStorage 参数
  * @returns 
  */
function set<T>(storage: Storage, key: string, value: T, option?: IOptionStorage<T>): void {
  ProxyConsole.debug(`storage.set：key = ${key}`);
  if (isBlank(value)) { return; }
  const stringifyValue = JSON.stringify(value);
  const newValue: string = option?.encrypt === true ? prefix + encryption(stringifyValue) : stringifyValue;
  storage.setItem(key, newValue);
}

function clear(storage: Storage): void {
  storage.clear();
}

function remove(storage: Storage, key: string): void {
  ProxyConsole.debug(`storage.remove：key = ${key}`);
  storage.removeItem(key);
}


/**
 * Storage 工具类
 */
class CustomStorage {
  private __storage: Storage;
  constructor(storage: Storage) {
    this.__storage = isEmpty(storage) ? globalThis.window?.localStorage : storage;
  }
  /**
   * storage 通过 Key 存储
   * @param key storage key
   * @param option { defaultValue：'默认值' }
   * @returns 返回目标
   */
  public get<T>(key: string, option?: IOptionStorage<T>): T {
    return get(this.__storage, key, option);
  }

  /**
   * storage set value
   * @param key storage key
   * @param value storage value
   * @param option IOptionStorage 参数
   * @returns 
   */
  public set<T>(key: string, value: T, option?: IOptionStorage<T>): void {
    set(this.__storage, key, value, option);
  }

  public clear(): void { clear(this.__storage); }

  public remove(key: string): void {
    remove(this.__storage, key);
  }

  /**
   * 向 keyRoot 对象追加 { [key]: value}
   * @param key storage key
   * @param attribute 属性名
   * @param value 属性值
   * @param option IOptionStorage 参数
   * @returns 
   */
  public put<T>(key: string, attribute: string, value: T, option?: IOptionStorage<T>): void {
    const target: any = get(this.__storage, key, Object.assign({}, option, { defaultValue: {} }));
    if (isObject(target)) {
      Reflect.set(target, attribute, value);
      set(this.__storage, key, target, option);
      return;
    }
    ProxyConsole.warn(`${key} value is not Object`);
  }

  /**
   * 获取 Map 元素
   * @param key storage key
   * @param attribute map key
   * @returns map value
   */
  public getAttribute<T>(key: string, attribute: string, option?: IOptionStorage<T>): T {
    const target: any = get(this.__storage, key, Object.assign({}, option, { defaultValue: {} }));
    if (isObject(target)) {
      const value: T = Reflect.get(target, attribute);
      return isEmpty(value)? option?.defaultValue : value;
    }
    return null;
  }
}

/**
 * Storage local 本地存储
 */
export const local: CustomStorage = new CustomStorage(globalThis.window?.localStorage);
/**
 * Storage session 会话存储
 */
export const session: CustomStorage = new CustomStorage(globalThis.window?.sessionStorage);