import { isBlank, isEmpty } from './Common';
import * as Type from './Type';

/**
* 不能为空
* @param target 目标
* @param message 异常信息
*/
export function notEmpty(target: any, message: string): void {
  if (isEmpty(target)) {
    throw new Error(message);
  }
}

/**
 * 必须为字符串
 * @param target 目标
 * @param message 异常信息
 */
export function isString(target: any, message: string): void {
  if (Type.isString(target)) { return; }
  throw new Error(message);
}

/**
 * 不能为空串
 * @param target 目标
 * @param message 异常信息
 */
export function notBlank(target: any, message: string): void {
  if (isBlank(target)) {
    throw new Error(message);
  }
}

/**
 * 必须为数组
 * @param target 目标
 * @param message 异常信息
 */
export function isArray(target: any, message: string): void {
  if (Type.isArray(target)) { return; }
  throw new Error(message);
}

/**
* 有效的数组
* @param {Array} target 目标
* @param {String} message 消息
*/
export function validArray(target: any, message: string): void {
  if (isBlank(target)) { throw new Error(message); }
}

/**
 * 是个对象
 * @param target 目标
 * @param message 消息
 */
export function isObject(target: any, message: string): void {
  if (Type.isObject(target) === false) { throw new Error(message); }
}


/**
 * 有效的对象
 * @param {Array} target 目标
 * @param {String} message 消息
 */
export function validObject(target: any, message: string) {
  if (isBlank(target)) { throw new Error(message); }
}
