import { EnumType } from './DefineEnum';

/**
 * 获取目标的数据类型
 * @param target 目标
 * @returns 
 */
export function getType(target: any): EnumType {
  return toString.call(target) as EnumType
}

/**
 * 判断：是否为 Date
 * @param target 目标
 * @returns 
 */
export function isDate(target: any): boolean {
  return getType(target) === EnumType.Date
}

/**
 * 判断：是否为 File
 * @param target 目标
 * @returns 
 */
export function isFile(target: any): boolean {
  return getType(target) === EnumType.File
}

/**
 * 判断：是否为 Blog
 * @param target 目标
 * @returns 
 */
export function isBlog(target: any): boolean {
  return getType(target) === EnumType.Blob
}

/**
 * 判断：是否为 boolean
 * @param target 目标
 * @returns 
 */
export function isBoolean(target: any): boolean {
  return getType(target) === EnumType.Boolean
}

/**
 * 判断：是否为 number
 * @param target 目标
 * @returns 
 */
export function isNumber(target: any): boolean {
  return getType(target) === EnumType.Number
}

/**
 * 判断：是否为 string
 * @param target 目标
 * @returns 
 */
export function isString(target: any): boolean {
  return getType(target) === EnumType.String
}


/**
 * 判断：是否为 object or array
 * @param target 目标
 * @returns 
 */
export function isObjectOrArray(target: any): boolean {
  return isObject(target) || isArray(target)
}

/**
 * 判断：是否为 object
 * @param target 目标
 * @returns 
 */
export function isObject(target: any): boolean {
  if((typeof target === 'object') === false) { return false }
  if ( target instanceof Array) { return false }
  return Object.isExtensible(target)
}


/**
 * 判断：是否为 array
 * @param target 目标
 * @returns 
 */
export function isArray(target: any): boolean {
  return target instanceof Array
}


/**
 * 判断：是否为 regExp
 * @param target 目标
 * @returns 
 */
export function isRegExp(target: any): boolean {
  return getType(target) === EnumType.Array
}

/**
 * 判断：是否为 function
 * @param target 目标
 * @returns 
 */
export function isFunction(target: any): boolean {
  return getType(target) === EnumType.Function
}

/**
 * 判断：是否为 async function
 * @param target 目标
 * @returns 
 */
export function isAsyncFunction(target: any): boolean {
  return getType(target) === EnumType.AsyncFunction
}

/**
 * 判断：是否为 promise
 * @param target 目标
 * @returns 
 */
export function isPromise(target: any): boolean {
  return getType(target) === EnumType.Promise
}

/**
 * 判断：是否为 error
 * @param target 目标
 * @returns 
 */
export function isError(target: any): boolean {
  return getType(target) === EnumType.Error
}