import { assign } from './Unclassified';
import { getType, isString } from './Type';
import { Axios, AxiosRequestConfig, AxiosResponse, InternalAxiosRequestConfig } from 'axios';
import Qs from 'qs';
import { ErrorConsole } from './CustomError';
import { isBlank } from './Common';
import { session } from './CustomStorage';
import { TypeConsole, TypeVoid } from './DefineType';
import { EnumConsole } from './DefineEnum';
import { ProxyConsole } from './DefineProxy';

/**
   * Header 常用 key
   */
export enum EnumHeader {
  Encoding = 'accept-encoding',
  Language = 'accept-language',
  ContentType = 'content-type',
  RequestedWith = 'X-Requested-With',
  Authorization = 'authorization',
  TokenAccess = 'access_token',
  TokenRefresh = 'refresh_token'
}

/**
 * Header ContentType value
 */
export enum EnumContentType {
  Json = 'application/json',
  Form = 'application/x-www-form-urlencoded',
}

/**
 * Header RequestedWith value
 */
export enum EnumRequestedWith { Ajax = 'XMLHttpRequest', }

/**
 * Header Language value
 */
export enum EnumLanguage { H = 'zh-CN' }

/**
 * Header Encoding value
 */
export enum EnumEncoding { }

export interface IBody<T = any> {
  success?: boolean;
  code?: string;
  message?: string;
  data?: T;
}

export interface IHeader {
  [EnumHeader.Encoding]?: string | EnumEncoding;
  [EnumHeader.Language]?: string | EnumLanguage;
  [EnumHeader.ContentType]?: string | EnumContentType;
  [EnumHeader.RequestedWith]?: string | EnumRequestedWith;
  [EnumHeader.Authorization]?: string;
  [EnumHeader.TokenAccess]?: string;
  [EnumHeader.TokenRefresh]?: string;
  [key: string]: any;
}

export interface ISecurity {
  enable: () => boolean;
  hookVerify: (body: IBody, headers: IHeader) => TypeVoid;
  hookResponseHeader: (headers: IHeader) => TypeVoid;
  hookResponseDataParseFailure: (body: any, headers: IHeader) => TypeVoid;
  hookLoginFailure: (body: IBody, headers: IHeader) => TypeVoid;
  hookUnauthorized: (body: IBody, headers: IHeader) => TypeVoid;
  hookErrorUnknown: (body: IBody, headers: IHeader) => TypeVoid;
}

export const HttpSecurity: ISecurity = {
  enable() { return false; },
  hookVerify(body: IBody, headers: any): TypeVoid {
    const code: string = body.code as string;
    if (this.enable() && isString(code)) {
      if (code.startsWith('TOKEN_') || code.startsWith('CREDENTIAL_')) {
        session.remove(EnumHeader.Authorization);
        this.hookLoginFailure(body, headers);
      }
      if (code === 'LOGINED_AUTHORITY') {
        this.hookUnauthorized(body, headers);
      }

      if (code.startsWith('ERROR_')) {
        this.hookErrorUnknown(body, headers);
      }
    }
  },
  hookResponseHeader(headers: any): TypeVoid {
    if (Reflect.has(headers, EnumHeader.Authorization)) {
      session.set(EnumHeader.Authorization, Reflect.get(headers, EnumHeader.Authorization));
    }
  },
  hookResponseDataParseFailure(body: any, headers: any): TypeVoid {
    throw new ErrorConsole(JSON.stringify({ body, headers }), EnumConsole.error);
  },
  hookLoginFailure(body: IBody, headers: any): TypeVoid {
    ProxyConsole.warn(`请实现回调函数：Security.${this.hookLoginFailure.name}`, body, headers);
    throw new ErrorConsole('回话失效, 请重新登录...', EnumConsole.info);
  },
  hookUnauthorized(body: IBody, headers: any): TypeVoid {
    ProxyConsole.warn(`请实现回调函数：Security.${this.hookUnauthorized.name}`, body, headers);
    throw new ErrorConsole('无权访问，请检查权限...', EnumConsole.info);
  },
  hookErrorUnknown(body: IBody, headers: any): TypeVoid {
    ProxyConsole.warn(`请实现回调函数：Security.${this.hookErrorUnknown.name}`, body, headers);
    throw new ErrorConsole(body.message as string, EnumConsole.error);
  }
};


function reject(error: any): Promise<any> {
  return Promise.reject(error);
}

const interceptorRequest = {
  resolve(config: any): any {
    ProxyConsole.debug('-----interceptor-Request------ config-before -> ', config);
    const headers: any = {
      [EnumHeader.ContentType]: EnumContentType.Json,
      [EnumHeader.RequestedWith]: 'XMLHttpRequest',
      [EnumHeader.Authorization]: session.get(EnumHeader.Authorization),
      [EnumHeader.TokenAccess]: session.get(EnumHeader.TokenAccess),
      [EnumHeader.TokenRefresh]: session.get(EnumHeader.TokenRefresh)
    };
    { /** 透传 国际化语言 */
      const language = session.get(EnumHeader.Language);
      if (isBlank(language) === false) {
        Reflect.set(headers, EnumHeader.Language, language);
      }
    }
    if (isBlank(config.headers) === false) {
      assign(headers, config.headers, { deep: true });
    }

    Reflect.set(config, 'headers', headers);

    { /** 根据 contentType 处理 data */
      const contentType: string = Reflect.get(headers, EnumHeader.ContentType);
      if (contentType === EnumContentType.Json) {
        Reflect.set(config, 'data', JSON.stringify(config.data));
      }
      if (contentType === EnumContentType.Form) {
        // paramsSerializer 滞后解析
      }
    }
    ProxyConsole.debug('-----interceptor-Request------ config-after ->', config);
    return config;
  },
  reject
};

const interceptorResponse = {
  resolve(response: any): any {
    const headers = response.headers;
    try {
      HttpSecurity.hookResponseHeader(headers);
      if (Reflect.has(headers, 'ignore-parse')) {
        return response.data;
      }
      if (isString(response.data) === false) {
        return HttpSecurity.hookResponseDataParseFailure(response.data, headers);
      }
      const body: IBody = JSON.parse(response.data) as IBody;

      if (body.success === true) { return body.data; }
      if (body.success === false) { HttpSecurity.hookVerify(body, headers); }

      return Promise.reject({ body, headers });
    } catch (error) {
      if (error instanceof ErrorConsole) {
        const fn: TypeConsole = Reflect.get(console, error.type);
        return fn.call(console, error);
      }
      if (error instanceof Error) {
        return Promise.reject(error.message);
      }
      if (error instanceof Promise) {
        return error;
      }
      return Promise.reject(error);
    }
  },
  reject
};

const defaultConfig: AxiosRequestConfig<any> = {
  baseURL: '/api/',
  timeout: 60000,
  paramsSerializer: {
    serialize (params: any) {
      return Qs.stringify(params, { arrayFormat: 'repeat' });
    }
  }
};
/**
 * Axios 扩展
 */
export default class Http extends Axios {
  constructor(config?: AxiosRequestConfig, userDefault?: boolean) {
    super(userDefault ? assign(defaultConfig, config || {}, { deep: true }) : config);
    if (userDefault) {
      ProxyConsole.debug('使用默认配置，并默认配置优先级较高...');
      this.setInterceptorToRequeset(interceptorRequest.resolve, interceptorRequest.reject);
      this.setInterceptorToResponse(interceptorResponse.resolve, interceptorResponse.reject);
    }
  }

  public setConfig(config: AxiosRequestConfig): void {
    assign(this.defaults, config, { deep: true });
  }

  public setInterceptorToResponse(resolve: (value: AxiosResponse<any, any>) => AxiosResponse<any, any>, reject?: (error: any) => any): void {
    this.interceptors.response.use(resolve, reject);
  }

  public setInterceptorToRequeset(resolve: (value: InternalAxiosRequestConfig<any>) => InternalAxiosRequestConfig<any>, reject?: (error: any) => any): void {
    this.interceptors.request.use(resolve, reject);
  }

  /**
   * get
   * @param url 请求 地址
   * @param params 请求数据
   * @param config 可选配置
   * @returns 
   */
  public get<T, R = AxiosResponse<T> | T>(url: string, params?: any, config?: any): Promise<R> {
    return this.request({ url, method: 'get', params, ...config });
  }

  /**
   * post
   * @param url 请求 地址
   * @param data 请求数据
   * @param config 可选配置
   * @returns 
   */
  public post<T, R = AxiosResponse<T> | T>(url: string, data?: any, config?: any): Promise<R> {
    return this.request({ url, method: 'post', data, ...config });
  }

  /**
   * put
   * @param url 请求 地址
   * @param data 请求数据
   * @param config 可选配置
   * @returns 
   */
  public put<T, R = AxiosResponse<T> | T>(url: string, data?: any, config?: any): Promise<R> {
    return this.request({ url, method: 'put', data, ...config });
  }

  /**
   * delete
   * @param url 请求 地址
   * @param params 请求数据
   * @param config 可选配置
   * @returns 
   */
  public delete<T, R = AxiosResponse<T> | T>(url: string, params?: any, config?: any): Promise<R> {
    return this.request({ url, method: 'delete', params, ...config });
  }
  /**
   * patch
   * @param url 请求 地址
   * @param data 请求数据
   * @param config 可选配置
   * @returns 
   */
  public patch<T, R = AxiosResponse<T> | T>(url: string, data?: any, config?: any): Promise<R> {
    return this.request({ url, method: 'patch', data, ...config });
  }
}

export const http: Http = new Http({}, true);