import { EnumConsole } from './DefineEnum';

/**
 * 控制台异常
 */
export class ErrorConsole extends Error {
  private __type: EnumConsole | undefined;
  /**
   * 控制台异常构造
   * @param message 异常信息
   * @param type 控制台类型
   */
  constructor(message: string, type?: EnumConsole) {
    super(message)
    this.__type = type;
  }
  /**
   * 获取控制台类型
   */
  public get type(): EnumConsole {
    return this.__type || EnumConsole.debug
  }
}

/**
 * 视图异常
 */
export class ErrorView extends Error {
  constructor(message: string) {
    super(message)
  }
}