import { isEmpty } from './Common';

/**
 * 转换为：数字类型
 * @param target 目标
 * @returns 
 */
export function toNumber(target: string): number {
  const value = target.replace(/[^\d]+/, '');
  return isEmpty(value) ? 0 : Number(value);
}

/**
 * 转换：小驼峰
 * @param target 目标字符串
 * @param connector 链接字符串，默认 '-'
 * @returns 
 */
export function toLowerCamelCase(target: string, connector = '-'): string {
  if (target.includes('/')) {
    return target.split('/').flatMap((item: string) => toLowerCamelCase(item, connector)).join('/');
  }
  return toFirstCharLowerCase(toUpperCamelCase(target, connector));
}

/**
 * 转换：大驼峰
 * @param target 目标字符串
 * @param connector 链接字符串，默认 '-'
 * @returns 
 */
export function toUpperCamelCase(target: string, connector = '-'): string {
  if (target.includes('/')) {
    return target.split('/').flatMap((item: string) => toUpperCamelCase(item, connector)).join('/');
  }
  return target.split(connector).flatMap((item: string) => toFirstCharUpperCase(item)).join('');
}

/**
 * 转换：拼接模式
 * @param target 目标字符串
 * @param connector 链接字符串，默认 '-'
 * @returns 
 */
export function toJoin(target: string, connector = '-'): string {
  const value: string = target.replace(/([A-Z])/g, $1 => connector + $1.toLowerCase()).replace(/\/-/g, '/');
  return value.startsWith(connector) ? value.substring(1) : value;
}

/**
 * 转换：路径格式化、标准化
 * @param target 
 * @returns 
 */
export function toPathFormat(target: string): string {
  return target.replace(/:\/+/g, '**').replace(/\\+/g, '/').replace(/\/+|\\+/g, '/').replace('**', '://');
}

/**
 * URL 绝对化
 * @param target 目标url
 * @param origin 默认当前环境
 * @returns
 */
export function toURL(target: string, origin?: string): URL {
  if (target.startsWith('http://') || target.startsWith('https://')) {
    return new URL(toPathFormat(target));
  }
  return new URL(toPathFormat(`${origin || location.origin}/${target}`));
}

/**
 * 转换：首字母转大写
 * @param {String} target 目标字符串
 * @returns
 */
export function toFirstCharUpperCase(target: string): string {
  if (isEmpty(target)) { return ''; }
  return target.slice(0, 1).toUpperCase() + target.slice(1);
}

/**
 * 转换：首字母转小写
 * @param {String} 目标字符串
 * @returns
 */
export function toFirstCharLowerCase(target: string): string {
  if (isEmpty(target)) { return ''; }
  return target.slice(0, 1).toLowerCase() + target.slice(1);
}