import { isBlank, isEmpty } from '../script/Common';
import { toURL } from '../script/Convert';
import { nextTick, onMounted, provide, Ref, ref, unref, watch } from 'vue';
import { session } from '../script/CustomStorage';
import { EnumHeader } from '../script/Http';
import { ProxyConsole } from '../script';
export interface IOptionTabPane {
  /**
   * 唯一编码：默认值 = origin + pathname + hash
   */
  code?: string;
  target?: string;
  mode?: string;
  closable?: boolean;
  refresh?: boolean;
  label: string;
  href?: string;
  children?: IOptionTabPane [];
}

enum EnumFrameAction {
  TabPaneAdd = 'tab-pane-add',
  TabPaneClose = 'tab-pane-close',
}

export interface IOptionFrame {
  /**
   * 缓存页签：刷新页面缓存最后一次激活的页签
   */
  cacheable?: boolean;
  sso?: boolean,
  /**
   * 最大页签数：超过最大值，自动删除第一个可关闭的页签
   */
  max?: number;
}

export interface IFrame {
  listRoute: Ref<IOptionTabPane[]>;
  activate: Ref<string>;
}
/**
 * 增加 Tab 页签
 * @param href 页面地址
 * @param option 参数
 */
export function tabPaneAdd(href: string, option?: IOptionTabPane) {
  const url: URL = toURL(href);
  ProxyConsole.debug(`gold-core：composable.Frame —— tabPaneAdd：${url.href}`);
  globalThis.top.postMessage(Object.assign(option || {}, { action: EnumFrameAction.TabPaneAdd, href: url.href }), '*');
}

/**
 * 关闭页签
 * @param code 页签编码 
 */
export function tabPaneClose(code: string | number) {
  globalThis.top.postMessage({ action: EnumFrameAction.TabPaneClose, code }, '*');
}

export function useFrame(option: IOptionFrame): IFrame {
  const listRoute = ref<IOptionTabPane[]>([]);
  const activate = ref<string>('');
  globalThis.window.addEventListener('message', ({ data }) => {
    if (data?.action === EnumFrameAction.TabPaneAdd) {
      return paneAdd(data, option);
    }
    if (data?.action === EnumFrameAction.TabPaneClose) {
      return paneClose(data);
    }
  });

  function paneAdd(data: IOptionTabPane, option?: IOptionFrame): boolean {
    framePaneAdd(data, option) 
    return true;
  }

  function framePaneAdd  (data: IOptionTabPane, option?: IOptionFrame){
    const { href, label, code, refresh, target, closable, mode } = data || {};
    const url: URL =  toURL(data?.href);
    if (target === '_blank') {
      globalThis.top.open(href);
      return true;
    }
    const newCode: string = isBlank(code) ? url.origin + url.pathname + url.hash : code;
    const targetPane: IOptionTabPane | undefined = unref(listRoute)?.find(({ code }) => newCode === code);
    
    if (option?.sso && url.origin !== globalThis.top.location.origin) {
      /** 添加 token 参数 */
      const searchParams: URLSearchParams = url.searchParams
      const keyToken = 'token';
      searchParams.delete(keyToken);
      searchParams.append(keyToken, session.get(EnumHeader.Authorization, { defaultValue: '' }));
    }
    
    if (targetPane === undefined || isBlank(targetPane)) {
      if (option.max && unref(listRoute).length >= option.max) {
        // 容量超过最大值，删除第一个可关闭的页签
        const index: number = unref(listRoute).findIndex(({ closable }) => closable === true);
        if (index >= 0) {
          unref(listRoute).splice(index, 1);
        }
      }
      unref(listRoute).push({ label, href: url.href, code: newCode, closable, mode });
    } else {
      if (refresh === true) {
        url.searchParams.set('v', new Date().getTime() + '');
      }
      
      Reflect.set(targetPane, 'href', url.href);
      Reflect.set(targetPane, 'label', label);
      Reflect.set(targetPane, 'refresh', refresh);
      Reflect.set(targetPane, 'closable', closable);
      Reflect.set(targetPane, 'code', code);
      Reflect.set(targetPane, 'mode', mode);
    }
    activate.value = newCode;
  }

  function paneClose(data: IOptionTabPane): void {
    const list: IOptionTabPane[] | undefined = unref(listRoute)?.filter(item => data.code !== item.code);
    if (list instanceof Array) {
      listRoute.value = list;
      /** 获取数组最后一个元素：倒叙数组获取第一个元素 */
      const [first] = [...list].reverse();
      activate.value = first.code as string;
    }
  }

  provide('list-route', listRoute);
  provide('tab-activate', activate);
  if (option.cacheable === true) {
    watch(() => activate.value, (value: string) => {
      const target: IOptionTabPane | undefined = unref(listRoute).find(item => item.code === value);
      if (target) {
        session.set<IOptionTabPane>('tab-activate', target);
      }
    });
    onMounted(async () => {
      await nextTick();
      const target: IOptionTabPane = session.get<IOptionTabPane>('tab-activate');
      if (!isEmpty(target)) {
        tabPaneAdd(target.href as string, target);
      }
    });
  }
  return { listRoute, activate };
}